import { createSlice } from "@reduxjs/toolkit"

const storedCart = localStorage.getItem("Cart")

const cartSlice  = createSlice({
    name : "Cart",
    initialState : {items : storedCart? JSON.parse("storedCart") : []},

    reducers:{
         handleIncreaseQuantity : (state) => {
            const item = state.items.find(i => i.id === action.payload);
            if(item){item.quantity += 1}
         },
         handleDecreaseQuantity : (state) => {
            const item = state.items.find(i => i.id === action.payload);
            if(item && item.quantity > 1) {item.quantity -= 1}
         },
         handleRemove : (state) => {
            state.items = state.items.filter (i => i.id !== action.payload)
         },
         handleQuantity : (state => {
            const {id , value} = action.payload;
            const item = state.items.find(i => i.id === id);
            if(item){item.quantity = value > 0 ? value : 1}
         })
    }
})

export const {handleIncreaseQuantity , handleDecreaseQuantity , handleRemove , handleQuantity}  = cartSlice.actions;

export default cartSlice.reducer;